
.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 4.4rem;
  text-align: center;
  font-family: 'Ma Shan Zheng', cursive;
}

.form {
  display: grid;
}

.label {
  font-size: 1.6rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  font-family: 'Ma Shan Zheng', cursive;
}

.input {
  background-color: #131b20;
  color: #fff;
  text-align: center;
  font-size: 1.6rem;
  font-family: inherit;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #444;
  margin-bottom: 1rem;
}

.button {
  background-color: rgba(0,0,0,0.75);
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  font-size: 1.4rem;
  font-family: 'Ma Shan Zheng', cursive;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgba(0, 0, 0, 0.85);
}

@media (min-width:786px){
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }
  .input {
    margin-bottom: 0;
  }
}

.card-list {
  margin-top: 4rem;
}

.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
  margin-bottom: 2rem;
  background-color: #252636;
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
  text-align: center;
}

.card--image {
  margin: 0 auto;
  display: block
}


